button {
    outline: none;
    overflow: hidden;
    position: relative;
}


button .ripple {
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.7);
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: ripple 0.6s linear;
            animation: ripple 0.6s linear;
}


@-webkit-keyframes ripple {
    to {
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        opacity: 0;
    }
}


@keyframes ripple {
    to {
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        opacity: 0;
    }
